@use "../../style.scss" as global;

.post {

	&-list {
		display: flex;
		justify-content: center;
		gap: 15px;
	}

	&-title {
		text-align: center;
	}

	&-subtitle {
		text-align: center;
	}

	&-item {
		box-shadow: 0 14px 7px rgb(0 0 0 / 25%);
		max-width: min-content;

		a {
			color: #000;
			text-decoration: none;
		}
	}
}

.posts {

	&-title {
		color: #000;
		text-decoration: none;
	}

	&-list {
		display: flex;
		gap: 10px;
	}
}

.narahosting-latest-posts-block {
	display: flex !important;
	flex-direction: column !important;

	h3 {
		color: global.$primary-color;
	}
}


@media (max-width: global.$breakpoint-tablet) {
  
	.posts-list {
		flex-direction: column;
		align-items: center;
	}
}
